@import "nav";

.header {
  height: 700px;
  max-width: 1260px;
  background-image: url(../images/upd/header-new.png);
  background-size: cover;
  background-position: center;
  position: relative;

  &__nav {
    position: absolute;
    bottom: 48px;
    left: 0;
    right: 0;
  }

  &__top {
    position: absolute;
    top: 24px;
    display: flex;
    justify-content: space-between;
    left: 120px;
    right: 120px;
    color: white;
  }

  &__phone {
    margin-top: 32px;
  }

  &__action {
    margin-top: 32px;

    .header__link {
      color: white;
      text-decoration: none;
      padding-bottom: 8px;
      border-bottom: 1px solid white;
      transition: color $hover-time, border-bottom $hover-time;

      &:hover {
        color: $hover-color;
        border-bottom: 1px solid $hover-color;
      }
    }
    
  }
}
